// app/i18n-provider.tsx
"use client"

import React, { createContext, useContext, useEffect, useMemo, useState } from "react"

type Translations = Record<string, any>

type I18nContextValue = {
  lang: "en" | "de"
  setLang: (l: "en" | "de") => void
  t: (key: string, fallback?: string) => string
  loaded: boolean
}

const I18nContext = createContext<I18nContextValue | undefined>(undefined)

async function fetchJson(path: string): Promise<Translations> {
  try {
    const res = await fetch(path, { cache: "no-store" })
    if (!res.ok) return {}
    return (await res.json()) as Translations
  } catch {
    return {}
  }
}

function getNested(obj: any, path: string) {
  if (!obj) return undefined
  const parts = path.split(".")
  let cur = obj
  for (const p of parts) {
    if (cur && Object.prototype.hasOwnProperty.call(cur, p)) cur = cur[p]
    else return undefined
  }
  return cur
}

export function I18nProvider({ children }: { children: React.ReactNode }) {
  const [lang, setLangState] = useState<"en" | "de">(() => {
    try {
      const stored = typeof window !== "undefined" ? localStorage.getItem("siteLang") : null
      if (stored === "de") return "de"
    } catch {}
    return "en"
  })

  const [enJson, setEnJson] = useState<Translations | null>(null)
  const [deJson, setDeJson] = useState<Translations | null>(null)
  const [loaded, setLoaded] = useState(false)

  useEffect(() => {
    let mounted = true
    ;(async () => {
      const [enData, deData] = await Promise.all([
        fetchJson("/locales/en.json"),
        fetchJson("/locales/de.json"),
      ])
      if (!mounted) return
      setEnJson(enData)
      setDeJson(deData)
      setLoaded(true)
    })()
    return () => {
      mounted = false
    }
  }, [])

  useEffect(() => {
    try {
      localStorage.setItem("siteLang", lang)
    } catch {}
  }, [lang])

  const setLang = (l: "en" | "de") => setLangState(l)

  const t = useMemo(() => {
    return (key: string, fallback?: string) => {
      const dict = lang === "en" ? enJson : deJson
      // if not loaded or dict missing, return fallback or key
      const val = getNested(dict, key)
      if (typeof val === "string") return val
      // if key missing but fallback provided, return fallback
      if (fallback) return fallback
      // final fallback: return the key (so UI still shows something)
      return key
    }
  }, [lang, enJson, deJson])

  const value: I18nContextValue = {
    lang,
    setLang,
    t,
    loaded,
  }

  return <I18nContext.Provider value={value}>{children}</I18nContext.Provider>
}

export function useTranslation() {
  const ctx = useContext(I18nContext)
  if (!ctx) {
    throw new Error("useTranslation must be used inside I18nProvider")
  }
  return ctx
}
